
import React from 'react';
import { TocItem } from '../utils/toc';
import { cn } from '../utils/cn';
import { ChevronRight, BookText } from 'lucide-react';

interface SidebarProps {
  toc: TocItem[];
  activeId: string;
  onItemClick: (id: string) => void;
  className?: string;
  viewMode?: 'standard' | 'appendix';
}

export const Sidebar: React.FC<SidebarProps> = ({ toc, activeId, onItemClick, className, viewMode = 'standard' }) => {
  const items = viewMode === 'appendix' 
    ? toc 
    : toc.filter(item => item.level === 1);

  return (
    <nav className={cn("h-full overflow-y-auto py-4 sm:py-6 px-3 sm:px-4 bg-gray-50/50", className)}>
      {viewMode === 'appendix' && (
        <div className="mb-3 sm:mb-4 px-1 sm:px-2">
          <div className="flex items-center gap-2 text-emerald-700 font-semibold text-sm sm:text-base">
            <BookText size={16} />
            <span></span>
          </div>
          <div className="mt-2 h-px bg-gradient-to-r from-emerald-200 to-transparent"></div>
        </div>
      )}
      <ul className="space-y-0.5 sm:space-y-1">
        {items.map((item) => {
          const displayText = viewMode === 'appendix' 
            ? item.text.replace('', '').trim() || item.text
            : item.text;
          
          return (
            <li key={item.id}>
              <button
                onClick={() => onItemClick(item.id)}
                title={item.text}
                className={cn(
                  "w-full text-left px-3 sm:px-4 py-2 sm:py-3 rounded-lg text-xs sm:text-sm font-medium transition-all duration-200 flex items-center justify-between group",
                  activeId === item.id
                    ? "bg-emerald-600 text-white shadow-md shadow-emerald-200"
                    : "text-gray-600 hover:bg-emerald-50 hover:text-emerald-700"
                )}
              >
                <span className="truncate">{displayText}</span>
                {activeId === item.id && <ChevronRight size={14} className="text-emerald-200" />}
              </button>
            </li>
          );
        })}
      </ul>
    </nav>
  );
};
