
import React, { useState, useEffect, useMemo, useRef } from 'react';
import { Menu, X, BookOpen, BookText, Pin, PinOff } from 'lucide-react';
import MarkdownRenderer from './components/MarkdownRenderer';
import { Sidebar } from './components/Sidebar';
import { TopNav } from './components/TopNav';
import { generateToc, TocItem, buildHierarchy, getAppendixMainItems, getAppendixSubItems, buildAppendixHierarchy } from './utils/toc';
import content from './assets/content.md?raw';
import appendixContent from './assets/appendix.md?raw';

type ViewMode = 'standard' | 'appendix';

function App() {
  const [isSidebarOpen, setIsSidebarOpen] = useState(false);
  const [isSidebarPinned, setIsSidebarPinned] = useState(false);
  const [activeId, setActiveId] = useState<string>('');
  const [viewMode, setViewMode] = useState<ViewMode>('standard');
  
  const currentContent = viewMode === 'standard' ? content : appendixContent;

  const toc = useMemo(() => generateToc(currentContent), [currentContent]);
  
  const appendixMainItems = useMemo(() => {
    if (viewMode !== 'appendix') return [];
    return getAppendixMainItems(toc);
  }, [toc, viewMode]);

  const [activeAppendixId, setActiveAppendixId] = useState<string>('');

  useEffect(() => {
    setActiveId('');
    window.scrollTo(0, 0);
    if (viewMode === 'appendix' && appendixMainItems.length > 0) {
      setActiveAppendixId(appendixMainItems[0].id);
    } else {
      setActiveAppendixId('');
    }
  }, [viewMode, appendixMainItems]);

  const activeL1Id = useMemo(() => {
    if (viewMode === 'appendix') return '';
    if (!activeId) return toc[0]?.id || '';
    
    const activeIndex = toc.findIndex(item => item.id === activeId);
    if (activeIndex === -1) return toc[0]?.id || '';
    
    for (let i = activeIndex; i >= 0; i--) {
      if (toc[i].level === 1) {
        return toc[i].id;
      }
    }
    return toc[0]?.id || '';
  }, [activeId, toc, viewMode]);

  const currentSubItems = useMemo(() => {
    if (viewMode === 'appendix') {
      if (!activeAppendixId) return [];
      const subItems = getAppendixSubItems(toc, activeAppendixId);
      return buildAppendixHierarchy(subItems);
    }
    
    if (!activeL1Id) return [];
    
    const l1Index = toc.findIndex(item => item.id === activeL1Id);
    if (l1Index === -1) return [];
    
    const items: TocItem[] = [];
    for (let i = l1Index + 1; i < toc.length; i++) {
      if (toc[i].level === 1) break;
      items.push(toc[i]);
    }
    
    return buildHierarchy(items);
  }, [activeL1Id, toc, viewMode, activeAppendixId]);

  const shouldShowTopNav = useMemo(() => {
    if (currentSubItems.length === 0) return false;
    const sectionsToHide = [''];
    const shouldHide = sectionsToHide.some(section => activeL1Id.includes(section));
    return !shouldHide;
  }, [currentSubItems, activeL1Id]);

  const mainRef = useRef<HTMLElement>(null);

  const handleItemClick = (id: string) => {
    const element = document.getElementById(id);
    if (element) {
       const offset = 140;
       const elementPosition = element.getBoundingClientRect().top + window.scrollY;
       
       window.scrollTo({
         top: elementPosition - offset,
         behavior: 'auto'
       });
       
       setActiveId(id);
       if (!isSidebarPinned) {
         setIsSidebarOpen(false);
       }
    }
  };

  const handleAppendixClick = (id: string) => {
    setActiveAppendixId(id);
    setActiveId('');
    handleItemClick(id);
  };

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            setActiveId(entry.target.id);
          }
        });
      },
      {
        root: null,
        rootMargin: '-100px 0px -60% 0px',
        threshold: 0
      }
    );

    const elements = toc.map(item => document.getElementById(item.id)).filter(Boolean);
    elements.forEach(el => observer.observe(el!));

    return () => observer.disconnect();
  }, [toc]);

  const sidebarToc = useMemo(() => {
    if (viewMode === 'appendix') {
      return appendixMainItems;
    }
    return toc;
  }, [toc, viewMode, appendixMainItems]);

  const sidebarActiveId = viewMode === 'appendix' ? activeAppendixId : activeL1Id;

  const toggleSidebarPin = () => {
    setIsSidebarPinned(!isSidebarPinned);
    if (!isSidebarPinned) {
      setIsSidebarOpen(true);
    }
  };

  return (
    <div className="min-h-screen bg-white flex flex-col font-sans text-gray-900">
      <header className="bg-white/90 backdrop-blur-md border-b border-gray-100 fixed top-0 left-0 right-0 z-50 h-14 sm:h-16 flex items-center justify-between px-3 sm:px-4 lg:px-6 shadow-sm">
        <div className="flex items-center">
            <button
            onClick={() => setIsSidebarOpen(!isSidebarOpen)}
            className="lg:hidden p-2 text-gray-600 hover:bg-gray-100 rounded-lg mr-2 sm:mr-3 transition-colors"
            >
            {isSidebarOpen ? <X size={22} /> : <Menu size={22} />}
            </button>
            <div className="flex items-center text-emerald-700">
            <div className="bg-emerald-100 p-1.5 sm:p-2 rounded-lg mr-2 sm:mr-3">
                <BookOpen className="text-emerald-600" size={18} />
            </div>
            <h1 className="text-base sm:text-lg lg:text-xl font-bold tracking-tight text-gray-900 truncate max-w-[140px] sm:max-w-[200px] lg:max-w-none">
                 <span className="text-emerald-600 font-medium text-sm sm:text-base ml-1 hidden sm:inline">(2022)</span>
            </h1>
            </div>
        </div>

        <div className="flex items-center gap-2">
            <button
              onClick={toggleSidebarPin}
              className={`lg:hidden p-2 rounded-lg transition-colors ${isSidebarPinned ? 'bg-emerald-100 text-emerald-700' : 'text-gray-500 hover:bg-gray-100'}`}
              title={isSidebarPinned ? '' : ''}
            >
              {isSidebarPinned ? <PinOff size={18} /> : <Pin size={18} />}
            </button>
            <div className="flex bg-gray-100 rounded-lg p-0.5 sm:p-1">
                <button 
                    onClick={() => setViewMode('standard')}
                    className={`px-2 sm:px-3 py-1 sm:py-1.5 text-xs sm:text-sm font-medium rounded-md transition-all ${viewMode === 'standard' ? 'bg-white text-emerald-700 shadow-sm' : 'text-gray-500 hover:text-gray-700'}`}
                >
                    
                </button>
                <button 
                    onClick={() => setViewMode('appendix')}
                    className={`px-2 sm:px-3 py-1 sm:py-1.5 text-xs sm:text-sm font-medium rounded-md transition-all flex items-center gap-1 ${viewMode === 'appendix' ? 'bg-white text-emerald-700 shadow-sm' : 'text-gray-500 hover:text-gray-700'}`}
                >
                    <BookText size={12} className="hidden sm:block" />
                    
                </button>
            </div>
        </div>
      </header>

      <div className="flex pt-14 sm:pt-16 min-h-screen">
        <aside className="hidden lg:block w-72 fixed top-14 sm:top-16 bottom-0 left-0 border-r border-gray-100 bg-gray-50/30 z-40">
           <Sidebar 
             toc={sidebarToc} 
             activeId={sidebarActiveId} 
             onItemClick={viewMode === 'appendix' ? handleAppendixClick : handleItemClick}
             viewMode={viewMode}
             className="h-full w-full"
           />
        </aside>

        {isSidebarPinned ? (
          <aside className={`fixed top-14 sm:top-16 bottom-0 left-0 w-[280px] sm:w-[300px] bg-white shadow-2xl z-50 lg:hidden`}>
            <div className="flex items-center justify-between p-3 sm:p-4 border-b border-gray-100">
              <h2 className="font-bold text-gray-900 text-base sm:text-lg">{viewMode === 'appendix' ? '' : ''}</h2>
              <div className="flex items-center gap-1">
                <button 
                  onClick={toggleSidebarPin} 
                  className="p-2 rounded-lg transition-colors bg-emerald-100 text-emerald-700"
                  title=""
                >
                  <PinOff size={18} />
                </button>
              </div>
            </div>
            <Sidebar 
               toc={sidebarToc} 
               activeId={sidebarActiveId} 
               onItemClick={viewMode === 'appendix' ? handleAppendixClick : handleItemClick}
               viewMode={viewMode}
               className="h-[calc(100%-60px)] sm:h-[calc(100%-65px)]"
            />
          </aside>
        ) : (
          <div className={`fixed inset-0 z-50 lg:hidden transition-opacity duration-300 ${isSidebarOpen ? 'opacity-100 pointer-events-auto' : 'opacity-0 pointer-events-none'}`}>
            <div 
              className="absolute inset-0 bg-black/20 backdrop-blur-sm"
              onClick={() => setIsSidebarOpen(false)}
            ></div>
            <aside className={`absolute top-14 sm:top-16 bottom-0 left-0 w-[280px] sm:w-[300px] bg-white shadow-2xl transform transition-transform duration-300 ${isSidebarOpen ? 'translate-x-0' : '-translate-x-full'}`}>
              <div className="flex items-center justify-between p-3 sm:p-4 border-b border-gray-100">
                <h2 className="font-bold text-gray-900 text-base sm:text-lg">{viewMode === 'appendix' ? '' : ''}</h2>
                <div className="flex items-center gap-1">
                  <button 
                    onClick={toggleSidebarPin} 
                    className="p-2 rounded-lg transition-colors text-gray-500 hover:bg-gray-100"
                    title=""
                  >
                    <Pin size={18} />
                  </button>
                  <button onClick={() => setIsSidebarOpen(false)} className="p-2 text-gray-500 hover:bg-gray-100 rounded-lg">
                    <X size={18} />
                  </button>
                </div>
              </div>
              <Sidebar 
                 toc={sidebarToc} 
                 activeId={sidebarActiveId} 
                 onItemClick={viewMode === 'appendix' ? handleAppendixClick : handleItemClick}
                 viewMode={viewMode}
                 className="h-[calc(100%-60px)] sm:h-[calc(100%-65px)]"
              />
            </aside>
          </div>
        )}

        <main className={`flex-1 w-full bg-white relative transition-all duration-300 ${isSidebarPinned ? 'lg:ml-72 ml-[280px] sm:ml-[300px]' : 'lg:ml-72'}`}>
          {shouldShowTopNav && (
            <TopNav 
              items={currentSubItems}
              activeId={activeId}
              onItemClick={handleItemClick}
              className="w-full"
            />
          )}

          <div className="max-w-4xl mx-auto px-3 sm:px-4 py-6 sm:py-8 lg:px-12 lg:py-10 pb-20 sm:pb-32">
             <MarkdownRenderer content={currentContent} />
             
             <footer className="mt-16 sm:mt-24 pt-8 sm:pt-12 border-t border-gray-100 text-center text-gray-400 text-xs sm:text-sm">
               <p> (2022) &copy; 2026 Digital Edition</p>
             </footer>
          </div>
        </main>
      </div>
    </div>
  );
}

export default App;
