
import GithubSlugger from 'github-slugger';

export interface TocItem {
  id: string;
  text: string;
  level: number;
}

export interface TocNode extends TocItem {
  children: TocNode[];
}

function formatLatexText(text: string): string {
  let result = text;
  result = result.replace(/\$([^$]+)\$/g, (_, content) => {
    return content
      .replace(/\\sim/g, '~')
      .replace(/\\text\{([^}]+)\}/g, '$1')
      .replace(/\\mathrm\{([^}]+)\}/g, '$1')
      .replace(/\\cdot/g, '')
      .replace(/\\times/g, '')
      .replace(/\\div/g, '')
      .replace(/\\pm/g, '')
      .replace(/\\leq/g, '')
      .replace(/\\geq/g, '')
      .replace(/\\neq/g, '')
      .replace(/\\approx/g, '')
      .replace(/\\infty/g, '')
      .replace(/\\sum/g, '')
      .replace(/\\prod/g, '')
      .replace(/\\int/g, '')
      .replace(/\^{([^}]+)}/g, '^$1')
      .replace(/_{([^}]+)}/g, '_$1')
      .replace(/\\/g, '');
  });
  return result;
}

export function generateToc(markdown: string): TocItem[] {
  const lines = markdown.split('\n');
  const toc: TocItem[] = [];
  const slugger = new GithubSlugger();

  lines.forEach((line) => {
    const match = line.match(/^(#{1,6})\s+(.+)$/);
    if (match) {
      const level = match[1].length;
      const rawText = match[2].trim();
      const text = formatLatexText(rawText);
      
      const id = slugger.slug(rawText);

      toc.push({ id, text, level });
    }
  });

  return toc;
}

export function buildHierarchy(items: TocItem[]): TocNode[] {
  const root: TocNode[] = [];
  let currentL2: TocNode | null = null;

  items.forEach(item => {
    if (item.level === 2) {
      currentL2 = { ...item, children: [] };
      root.push(currentL2);
    } else if (item.level === 3 && currentL2) {
      currentL2.children.push({ ...item, children: [] });
    } else if (item.level === 3 && !currentL2) {
      root.push({ ...item, children: [] });
    }
  });
  
  return root;
}

export function getAppendixMainItems(toc: TocItem[]): TocItem[] {
  return toc.filter(item => item.level === 2 && item.text.startsWith(''));
}

export function getAppendixSubItems(toc: TocItem[], activeAppendixId: string): TocItem[] {
  const appendixIndex = toc.findIndex(item => item.id === activeAppendixId);
  if (appendixIndex === -1) return [];
  
  const items: TocItem[] = [];
  for (let i = appendixIndex + 1; i < toc.length; i++) {
    if (toc[i].level === 2 && toc[i].text.startsWith('')) break;
    if (toc[i].level === 2 || toc[i].level === 3) {
      items.push(toc[i]);
    }
  }
  
  return items;
}

export function buildAppendixHierarchy(items: TocItem[]): TocNode[] {
  const root: TocNode[] = [];
  let currentL2: TocNode | null = null;

  items.forEach(item => {
    if (item.level === 2) {
      currentL2 = { ...item, children: [] };
      root.push(currentL2);
    } else if (item.level === 3 && currentL2) {
      currentL2.children.push({ ...item, children: [] });
    } else if (item.level === 3 && !currentL2) {
      root.push({ ...item, children: [] });
    }
  });
  
  return root;
}
