
import React, { useEffect, useRef, useState } from 'react';
import { createPortal } from 'react-dom';
import { TocNode } from '../utils/toc';
import { cn } from '../utils/cn';
import { ChevronDown } from 'lucide-react';

interface TopNavProps {
  items: TocNode[];
  activeId: string;
  onItemClick: (id: string) => void;
  className?: string;
}

export const TopNav: React.FC<TopNavProps> = ({ items, activeId, onItemClick, className }) => {
  const scrollRef = useRef<HTMLDivElement>(null);
  const [openDropdownId, setOpenDropdownId] = useState<string | null>(null);
  const [dropdownPosition, setDropdownPosition] = useState<{ top: number; left: number } | null>(null);

  useEffect(() => {
    if (activeId && scrollRef.current) {
      let targetId = activeId;
      const isRoot = items.some(item => item.id === activeId);
      if (!isRoot) {
        const parent = items.find(item => item.children.some(child => child.id === activeId));
        if (parent) targetId = parent.id;
      }

      const activeElement = scrollRef.current.querySelector(`[data-id="${targetId}"]`);
      if (activeElement) {
        activeElement.scrollIntoView({
          behavior: 'smooth',
          block: 'nearest',
          inline: 'center'
        });
      }
    }
  }, [activeId, items]);

  useEffect(() => {
    const handleResize = () => {
      if (openDropdownId) {
        setOpenDropdownId(null);
      }
    };
    
    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, [openDropdownId]);

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      const target = event.target as HTMLElement;
      if (target.closest('.dropdown-content') || target.closest(`[data-id="${openDropdownId}"]`)) {
        return;
      }
      setOpenDropdownId(null);
    };
    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, [openDropdownId]);

  if (items.length === 0) return null;

  const handleMainClick = (e: React.MouseEvent<HTMLButtonElement>, item: TocNode) => {
     onItemClick(item.id);
     
     if (item.children.length > 0) {
        if (openDropdownId === item.id) {
            setOpenDropdownId(null);
        } else {
            const rect = e.currentTarget.getBoundingClientRect();
            setDropdownPosition({
                top: rect.bottom + 8,
                left: rect.left
            });
            setOpenDropdownId(item.id);
        }
     } else {
        setOpenDropdownId(null);
     }
  };

  return (
    <div className={cn("sticky top-14 sm:top-16 z-30 bg-white/95 backdrop-blur-sm border-b border-emerald-100 shadow-sm transition-all duration-300", className)}>
      <div 
        ref={scrollRef}
        className="flex overflow-x-auto py-2 sm:py-3 px-3 sm:px-4 gap-2 sm:gap-3 no-scrollbar scroll-smooth items-center max-w-7xl mx-auto"
      >
        {items.map((item) => {
           const isActive = activeId === item.id || item.children.some(child => child.id === activeId);
           const hasChildren = item.children.length > 0;
           const isOpen = openDropdownId === item.id;

           return (
            <div key={item.id} className="relative flex-shrink-0">
              <button
                data-id={item.id}
                onClick={(e) => handleMainClick(e, item)}
                title={item.text}
                className={cn(
                  "whitespace-nowrap px-3 sm:px-4 py-1 sm:py-1.5 rounded-full text-xs sm:text-sm font-medium transition-all duration-200 border flex items-center gap-1",
                  isActive
                    ? "bg-emerald-100 text-emerald-800 border-emerald-200 shadow-sm"
                    : "bg-white text-gray-600 border-gray-100 hover:bg-gray-50 hover:text-emerald-600 hover:border-emerald-100"
                )}
              >
                {item.text}
                {hasChildren && <ChevronDown size={12} className={cn("transition-transform", isOpen ? "rotate-180" : "")} />}
              </button>
            </div>
          );
        })}
      </div>

      {openDropdownId && dropdownPosition && createPortal(
        <div 
            className="dropdown-content fixed z-50 bg-white rounded-xl shadow-xl border border-gray-100 py-2 animate-in fade-in zoom-in-95 duration-200"
            style={{ 
                top: dropdownPosition.top, 
                left: Math.min(dropdownPosition.left, window.innerWidth - 240),
                minWidth: '200px',
                maxWidth: '280px'
            }}
        >
            {items.find(i => i.id === openDropdownId)?.children.map(child => (
                <button
                    key={child.id}
                    onClick={() => {
                        onItemClick(child.id);
                        setOpenDropdownId(null);
                    }}
                    className={cn(
                        "block w-full text-left px-4 py-2 sm:py-2.5 text-xs sm:text-sm hover:bg-emerald-50 transition-colors border-l-2 border-transparent",
                        activeId === child.id ? "text-emerald-700 font-medium bg-emerald-50 border-emerald-500" : "text-gray-600"
                    )}
                >
                    {child.text}
                </button>
            ))}
        </div>,
        document.body
      )}
    </div>
  );
};
